<template>
  <v-card>
    <v-card-title>
      每日任務
      <v-spacer></v-spacer>
      <small>
        <div>
          {{ date }} {{ className}}
          <v-icon @click="calendarOpen=!calendarOpen">mdi-calendar</v-icon>
        </div>
      </small>
    </v-card-title>
    <v-card-text>
      <v-expand-transition>
        <v-date-picker v-show="calendarOpen" v-model="date" full-width></v-date-picker>
      </v-expand-transition>
      <v-timeline>
        <v-timeline-item
          v-for="task in tasks"
          :key="task.title"
          :color="task.color"
          :small="task.small"
          :class="'scrollToHere'"
        >
          <v-card :color="task.color">
            <v-card-title>
              <span class="text-body-1 white--text">{{ task.time }} {{ task.title }}</span>
            </v-card-title>
            <v-card-text>
              <v-btn icon :to="task.route">
                <v-icon>{{ task.icon }}</v-icon>
              </v-btn>
              <br />
              <span>{{ task.detail }}</span>
            </v-card-text>
          </v-card>
        </v-timeline-item>
      </v-timeline>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data() {
    let todayDate = new Date().toLocaleDateString().replace(/\//g, "-");
    return {
      className: "小熊幼幼班",
      date: todayDate,

      calendarOpen: false,

      tasks: [
        {
          time: "08:00",
          title: "點名",
          detail: "",
          icon: "mdi-table-edit",
          color: "red",
          route: "/quest/rollcall",
        },
        {
          time: "10:00",
          title: "課程",
          detail: "繪本 小熊愛唱歌",
          icon: "mdi-book-open-page-variant",
          color: "orange",
        },
        {
          time: "11:30",
          title: "餵奶",
          detail: "",
          icon: "mdi-food-variant",
          color: "red",
        },
        {
          time: "13:00",
          title: "保育任務",
          detail: "",
          icon: "mdi-robot",
          color: "blue",
          small: true,
        },
        {
          time: "14:30",
          title: "團康活動",
          detail: "",
          icon: "mdi-cake",
          color: "green",
        },
        {
          time: "16:30",
          title: "清潔",
          detail: "",
          icon: "mdi-leaf",
          color: "green",
          route: "/quest/cleanup",
        },
        {
          time: "18:30",
          title: "個別觀察訪談",
          detail: "",
          icon: "mdi-phone",
          color: "warning",
          route: "/quest/interview/observation",
        },
      ],
    };
  },
  created() {
    let now = new Date();
    let index = this.tasks.findIndex(
      (task) => new Date(this.date + " " + task.time) - now > 0
    );
    this.tasks.splice(index, 0, {
      time: "",
      title: "缺席訪談",
      detail: "",
      icon: "mdi-phone",
      color: "blue",
      small: true,
      scroll: true,
      route: "/quest/interview/absent",
    });
  },
  mounted() {
    this.$vuetify.goTo(".scrollToHere");
  },
};
</script>

